@import './base.scss';

nav {
  width: 100%;

  ul{
    width: 100%;
    display: flex;
    height: 40px;
    flex-direction: row;
    background-color: white;
    justify-content: space-evenly;
    align-items: center;
  }

  li{
    height: 100%;
    width: 10vw;
    background-color: white;
    height: 100%;
    align-items: center;
    justify-content: center;
    display: flex;
  }

  a {
    text-decoration: none;
    color: black;
    &:hover {
      color: black;
      background-color: $hover-blue;
      padding: 3px;
    }
  }
  
}
