body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}

*{box-sizing:border-box;font-family:sans-serif}main{margin:0 5% 0 5%;display:grid;grid-gap:15px;gap:15px;grid-template-columns:32% 1fr;grid-template-rows:40px 150px 1fr;min-height:calc(70vh - 45px)}h1{font-size:3em;font-family:'Plaster', cursive;margin-bottom:30px}h2{grid-column:1 / span 2;font-size:1.5em;margin:20px 0 0 0}

*{box-sizing:border-box;font-family:sans-serif}main{margin:0 5% 0 5%;display:grid;grid-gap:15px;gap:15px;grid-template-columns:32% 1fr;grid-template-rows:40px 150px 1fr;min-height:calc(70vh - 45px)}h1{font-size:3em;font-family:'Plaster', cursive;margin-bottom:30px}h2{grid-column:1 / span 2;font-size:1.5em;margin:20px 0 0 0}header{background-color:#15336f;height:25vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:20px;color:white}

*{box-sizing:border-box;font-family:sans-serif}main{margin:0 5% 0 5%;display:grid;grid-gap:15px;gap:15px;grid-template-columns:32% 1fr;grid-template-rows:40px 150px 1fr;min-height:calc(70vh - 45px)}h1{font-size:3em;font-family:'Plaster', cursive;margin-bottom:30px}h2{grid-column:1 / span 2;font-size:1.5em;margin:20px 0 0 0}fieldset{height:100%;border-radius:5px;grid-column:1 / span 1;border:1px solid black;padding:10px}fieldset:nth-child(2){grid-column:2 / span 1;grid-row:1 / span 2}fieldset:last-of-type{align-self:right;grid-column:1 / span 1}#code-area{border:20px solid black}textarea{resize:none;height:90%;width:100%}.form{display:grid;grid-template-columns:50% 1fr 100px;grid-gap:15px;gap:15px;width:100%;justify-items:stretch;align-items:stretch;grid-column:1 / span 2}.form fieldset div{position:relative;height:100%;display:grid;grid-template-columns:1fr 1fr 1fr 1fr;justify-content:stretch;align-content:center}.form legend{font-size:1.1em;font-weight:500;padding:0 10px}.form input[placeholder="http://"]{width:100%;font-size:1.1em}.form input[type="radio"]{opacity:0;position:absolute;height:100%;width:100%;margin:0 auto;bottom:0px}.form input[type="radio"]:nth-of-type(1){grid-column:1/span 1}.form input[type="radio"]:nth-of-type(2){grid-column:2/span 1}.form input[type="radio"]:nth-of-type(3){grid-column:3/span 1}.form input[type="radio"]:nth-of-type(4){grid-column:4/span 1}.form input[type="radio"]:checked+label{background-color:#15336f;color:white;font-weight:bold}.form input[type="radio"]:checked+label:hover+label{background-color:#15336f;color:red}.form input[type="radio"]+label{margin:0 0 5px 0;padding:5px;text-align:center;border-radius:3px}.form input[type="radio"]:not(:checked):hover+label{background-color:#acacf0;cursor:pointer}.form button{justify-self:stretch;align-self:center;background-color:#15336f;color:white;font-size:1.5em;border-radius:5px;height:70%}.form button:hover{cursor:pointer}

*{box-sizing:border-box;font-family:sans-serif}main{margin:0 5% 0 5%;display:grid;grid-gap:15px;gap:15px;grid-template-columns:32% 1fr;grid-template-rows:40px 150px 1fr;min-height:calc(70vh - 45px)}h1{font-size:3em;font-family:'Plaster', cursive;margin-bottom:30px}h2{grid-column:1 / span 2;font-size:1.5em;margin:20px 0 0 0}#results{grid-column:2 / span 1}#results pre{max-height:40vh;border:1px solid black;padding:3px}#results pre div{padding:2px;font-family:monospace}#logo{width:100%;display:flex;justify-content:center}aside{margin:10px 0;grid-row:3 / span 2;overflow-x:scroll}aside.history{grid-row:2 / span 2;overflow-x:visible}aside li{list-style-type:none;margin-bottom:20px;display:inline-flex;width:100%}aside li span{padding:6px;text-align:center;border-radius:4px;border:1px solid black;margin-right:5px}aside li button{width:100%;background-color:white;border-radius:5px;border:solid 1px black}aside .GET{background-color:#5ff75f}aside .GET+button:hover{background-color:rgba(95,247,95,0.2)}aside .POST{background-color:#f9c35f}aside .POST+button:hover{background-color:rgba(249,195,95,0.2)}aside .DELETE{background-color:#fa8874}aside .DELETE+button:hover{background-color:rgba(250,136,116,0.2)}aside .PUT{background-color:#a1a1fa}aside .PUT+button:hover{background-color:rgba(161,161,250,0.2)}aside h2{margin:0 0 20px 0}aside ul{padding-left:0;width:100%}

*{box-sizing:border-box;font-family:sans-serif}main{margin:0 5% 0 5%;display:grid;grid-gap:15px;gap:15px;grid-template-columns:32% 1fr;grid-template-rows:40px 150px 1fr;min-height:calc(70vh - 45px)}h1{font-size:3em;font-family:'Plaster', cursive;margin-bottom:30px}h2{grid-column:1 / span 2;font-size:1.5em;margin:20px 0 0 0}footer{background-color:#15336f;height:10vh;width:100%;color:white;display:flex;align-items:center;justify-content:center;position:absolute;margin-top:20px;font-family:sans-serif}

*{box-sizing:border-box;font-family:sans-serif}main{margin:0 5% 0 5%;display:grid;grid-gap:15px;gap:15px;grid-template-columns:32% 1fr;grid-template-rows:40px 150px 1fr;min-height:calc(70vh - 45px)}h1{font-size:3em;font-family:'Plaster', cursive;margin-bottom:30px}h2{grid-column:1 / span 2;font-size:1.5em;margin:20px 0 0 0}nav{width:100%}nav ul{width:100%;display:flex;height:40px;flex-direction:row;background-color:white;justify-content:space-evenly;align-items:center}nav li{height:100%;width:10vw;background-color:white;height:100%;align-items:center;justify-content:center;display:flex}nav a{text-decoration:none;color:black}nav a:hover{color:black;background-color:#acacf0;padding:3px}

